* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: "Poppins", sans-serif;
}

.gray {
  color: #918e9b;
}

/* NAVBAR */
.navbar {
  height: 5rem;
  display: flex;
  align-items: center;
  box-shadow: 0px 2.98256px 7.4564px rgba(0, 0, 0, 0.1);
  padding: 2rem;
}
.nav--logo {
  max-width: 6rem;
}

section {
  padding: 2rem;
}

/* NAVBAR SECTION */
.hero {
  width: 100vw;
  display: flex;
  flex-direction: column;
}

.hero--img {
  max-width: 80%;
  min-width: 300px;
  align-self: center;
}

.hero > .hero--title {
  font-size: 2.5rem;
  margin-bottom: 1rem;
}
.hero > .hero--paragraph {
  max-width: 60%;
  margin-top: 0;
}

/* CARD SECTION */
.cards-list {
  display: flex;
  flex-wrap: nowrap;
  gap: 20px;
  overflow-x: auto;
}

.card {
  width: 175px;
  font-size: 12px;
  flex: 0 0 auto; /* ADD */
  display: flex; /* ADD */
  flex-direction: column; /* ADD */
  position: relative;
}
.card div {
  margin-bottom: 0.2rem;
}

.card--badge {
  position: absolute;
  top: 6px;
  left: 6px;
  background-color: white;
  padding: 5px 7px;
  border-radius: 2px;
  font-weight: bold;
}

.card--image {
  width: 100%;
  border-radius: 9px;
  margin-bottom: 0.5rem;
}

.card--raitings {
  display: flex;
  align-items: center;
  margin-bottom: 0.5rem;
}

.card--raitings span {
  margin-right: 0.2rem;
}

.card--raitings--star {
  width: 0.9rem;
}

.card--price span {
  font-weight: bold;
}
